.works {
  padding: 55px 0 90px;
  font-weight: bold;

  // &-list {
  //   display: flex;
  //   justify-content: center;
  //   align-items: flex-start;
  //   width: 100%;
  //   max-width: 1366px;
  //   overflow: hidden;
  //   margin: 70px auto 0;
  // }
  &__item {
    width: 320px;
    height: 400px;
    border: 1px solid $works__border_color;
    border-radius: 8px;
    &-wrap {
      padding: 20px 20px;
    }
  }
  &__heading {
    margin-bottom: 30px;
    line-height: lineHeight(24);
  }
  &__about {
    font-size: 1.4rem;
    line-height: lineHeight(20, 14);
  }
  img {
    max-width: 100%;
    width: 100%;
  }
}
// swiper
.swiper-container {
  max-width: 1366px;
