  font-weight: bold;
}

@media only screen and (max-width: 768px) {
  .navGlobal {
    display: none;
    background-color: #fff;
    height: 100%;
    padding: 30px 0;
  }
}

@media only screen and (max-width: 768px) {
  .navGlobal_sp {
    display: block;
    text-align: center;
  }
}

.navGlobal-list {
