$main_color: #333;
$color1: #fff;
$color2: #444;
$sub-title: #3BA6C9;
$shadow: #707070;
$border_color: #E5E5E5;
$works__border_color: #DDD;
$box_shadow: #00000029;
$attention: #0C449B;
$bg-section: #EEFBFB;
$bg-faq: #F2F2F2;
$bg-footer: #F8F8F8;
$linearGradient: linear-gradient(90deg, #05A5FA 0%, #63FBD7 90%, #67FFD6 100%);
$btn_linearGradient: linear-gradient(to right, #FA41CC, #6020B0);

@mixin section-inner {
  width: 1366px;
  margin: 0 auto;
}
@mixin width {
  width: 1024px;
}

// z-index
$layout: (
  header: "2",
  nav-button: "3",
);
@mixin z-index($key) {
  z-index: #{map-get($layout, $key)}
}
// line-height計算
$main-font: 16;
@function lineHeight($lineHeight, $fontSize: $main-font) {
  @return floor($lineHeight / $fontSize * 10) / 10;
}
// メディアクエリ
$breakpoints: (
  breakXS: "only screen and (max-width: 568px)",
  breakS: "only screen and (max-width: 768px)",
  breakM: "only screen and (max-width: 1024px)",
);
@mixin media($breakpoint) {
  @media #{map-get($breakpoints, $breakpoint)} {
    @content;
  }
}