.faq {
  padding: 55px 0 85px;
  font-weight: bold;
  @include media(breakS) {
    padding: 55px 10px 70px; 
  }
  &-list {
    max-width: 930px;
    border-bottom: 1px solid $border_color;
    margin: 32px auto 0;
    font-size: 1.4rem;
    &_q {
      padding: 20px 0;
      border: 1px solid $border_color;
      background-color: $color1;
      text-align: center;
      line-height: lineHeight(26,14);
      cursor: pointer;
      &:active {
        background-color: $border_color;
      }
      &::selection {
        color: none;
      }
    }
    &_a {
      display: none;
      padding: 20px 35px;
      border: 1px solid $border_color;
      background-color: $bg-faq;
      line-height: lineHeight(26,14);
      border-top: none;
      border-bottom: none;
    }
  }
}
.d-block {
  display: block;
}