.header {
  width: 100%;
  height: auto;
  top: 0;
  left: 0;
  box-shadow: 0 3px 6px $box_shadow;
  transition: background-color .15s;
  @include z-index(header);
  @include media(breakS) {
    position: fixed;
    box-shadow: none;
  }
  body.is-fixed & {
    position: fixed;
    background-color: rgba(255,255,255, .9);
    animation: header-height .5s forwards;
    @include media(breakS) {
      animation: none;
      background-color: transparent;
    }  
  }
}
.navGlobal-sp {
  display: none;
  flex-direction: column;
  align-items: center;
  background-color: #fff;
  padding: 50px 0;
  transform: translateY(-100vh);
  transition: transform .6s;
  @include media(breakS) {
    display: flex;
  }
}
.d-flex {
  display: flex;
}
.navGlobal {
  display: flex;
  justify-content: space-between;
  max-width: 1022px;
  height: 94px;
  padding: 0 20px;
  margin: 0 auto;
  font-weight: bold;

  @include media(breakS) {
    display: none;
    background-color: #fff;
    height: 100%;
    padding: 30px 0;
  }
  &_sp {
    @include media(breakS) {
      display: block;
      text-align: center;
    }
  }
  &-list {
    display: flex;
    align-items: center;
    @include media(breakS) {
      justify-content: center;
      padding: 20px;
    }
  }
  &__item {
    padding-right: 46px;
    line-height: lineHeight(24);
    @include media(breakS) {
      padding: 15px 0 15px 0;
    }  
  }
  &__item:last-of-type {
    padding-right: 0;
    margin-right: 50px;
    @include media(breakS) {
      margin-right: 0;
    }  
  }
  &__title {
    margin-left: 16px;
    font-size: 2.4rem;
    line-height: lineHeight(35, 24);
    @include media(breakM) {
      font-size: 2.2rem;
    }
  }
}
.nav-button {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  padding: 25px 16px;
  @include z-index(nav-button);
  @include media(breakS) {
    display: block;
  }
  &_line {
    position: relative;
    display: block;
    background-color: #333;
    width: 24px;
    height: 2px;
    text-align: center;
    transition: all .3s ease-in-out;
    &::before,
    &::after {
      position: absolute;
      content: '';
      display: block;
      background-color: #333;
      width: 24px;
      height: 2px;
      transition: all .3s ease-in-out;
    }
    &::before {
      top: -8px;
    }
    &::after {
      bottom: -8px;
    }
  }
}
.is-nav-open.nav-button_line {
  background-color: transparent;
}
.is-nav-open.nav-button_line::before {
  transform: rotate(-45deg);
  top: 0;
}
.is-nav-open.nav-button_line::after {
  transform: rotate(45deg);
  bottom: 0;
}