.footer {
  width: 100%;
  padding: 45px 0 50px;
  background-color: $bg-footer;
  text-align: center;
  font-weight: bold;
  @include media(breakS) {
    padding: 30px 0 15px; 
  }
  &-wrap {
    display: flex;
    justify-content: center;
    margin-bottom: 50px;
    @include media(breakS) {
      margin-bottom: 20px;
    }
  }
  &__title {
    margin-bottom: 20px;
    font-size: 2.4rem;
    line-height: lineHeight(28, 24);
    @include media(breakS) {
      margin-bottom: 10px;
      font-size: 2.2rem;
    }
  }
  &__heading {
    margin: 0 10px;
    line-height: lineHeight(28);
  }
}
.copyright {
  font-size: 1.4rem;
  font-weight: normal;
  line-height: lineHeight(20, 14);
  @include media(breakS) {
    font-size: 1.2rem; 
  }
}