.price {
  width: 100%;
  padding: 55px 0 70px 0;
  background-color: $bg-section;
  font-weight: bold;
  @include media(breakS) {
    padding: 55px 10px 70px; 
  }

  &__table {
    display: table;
    table-layout: fixed;
    max-width: 930px;
    margin: 70px auto 35px;
    background-color: $color1;
    box-shadow: 0 3px 6px $box_shadow;
    line-height: lineHeight(28);
    tr > th {
      width: 35%;
      padding: 35px 20px;
      border: 1px solid $border_color;
    }
    tr > td {
      width: 65%;
      padding-left: 30px;
      border: 1px solid $border_color;
    }
    @include media(breakS) {
      margin: 35px auto;
    }
  }
  &_alert {
    text-align: center;
  }
}