.features {
  margin: 150px 0;
  font-weight: 600;
  @include media(breakS) {
    margin: 80px 0; 
  }

  &__title,
  &__sub-title {
    text-align: center;
  }
  & .section__title {
    line-height: lineHeight(46, 32);
  }
  &__sub-title {
    margin-bottom: 65px;
    font-size: 2.8rem;
    line-height: lineHeight(40, 28);
    &:nth-of-type(1) {
      margin-top: 57px;
      @include media(breakM) {
        margin-top: 30px;
      }
    }
    @include media(breakM) {
      margin-bottom: 50px;
      font-size: 2.4rem;
    }
    @include media(breakS) {
      font-size: 2.2rem;
    }
  }
  &-desc {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;
    max-width: 1024px;
    margin: 0 auto 90px;
    &:nth-of-type(2) {
      margin-bottom: 65px;
    }
    &:nth-of-type(3) {
      margin-bottom: 65px;
    }
    &__heading {
      margin-bottom: 50px;
      font-size: 2.4rem;
      line-height: lineHeight(48, 24);
      @include media(breakS) {
        font-size: 2.2rem;
      }
    }
    &__text {
      line-height: lineHeight(26);
    }
    @include media(breakM) {
      display: block;
      text-align: center;
    }
  }
  & &-desc:nth-of-type(odd) {
    flex-direction: row-reverse;
  }
  & &-desc:nth-of-type(even) {
    flex-direction: row;
  }
  &__image {
    max-width: 100%;
    height: auto;
    @include media(breakM) {
      margin: 0 auto 50px; 
    }
  }
}
