.mainView {
  position: relative;
  max-width: 1366px;
  height: 674px;
  margin: 0 auto;
  background: $linearGradient;
  overflow: hidden;
  &-img {
    position: absolute;
  }
  &-img.circle {
    top: -110px;
    right: -110px;
    @include media(breakS) {
      display: none;
    }
  }
  &-img.human {
    top: 150px;
    left: 50%;
    @include media(breakS) {
      display: none;
    }
  }
  &-desc {
    position: absolute;
    top: 160px;
    left: 14%;
    font-weight: bold;
    @include media(breakS) {
      left: 0;
      width: 100%;
      text-align: center;
    }

    &__title {
      margin-bottom: 45px;
      font-size: 3.2rem;
      line-height: lineHeight(46, 32);
      @include media(breakM) {
        font-size: 2.8rem;
      }
    }
    &__text {
      margin-bottom: 45px;
      line-height: lineHeight(24);
    }
  }
}
