.contact {
  padding: 70px 0;
  background: $linearGradient;
  font-weight: bold;

  .section__title,
  .section__title-eg,
  &__heading {
    color: $color1;
  }
  &-form {
    max-width: 910px;
    padding: 35px 60px;
    margin: 0 auto;
    background-color: $color1;
    @include media(breakS) {
      max-width: 100%;
      padding: 35px 20px;
    }
    span {
      display: inline-block;
    }
    label {
      display: block;
      text-align: center;
    }
  }
  &__heading {
    margin-top: 35px;
    margin-bottom: 45px;
    text-align: center;
    line-height: lineHeight(24);
  }
}
.form {
  &__title {
    position: relative;
    text-align: center;
    line-height: lineHeight(26);
  }
  &__title::after {
    position: absolute;
    top: -50%;
    content: '必須';
    display: inline-block;
    width: 60px;
    padding: 4px 0;
    margin-left: 20px;
    border-radius: 10px;
    font-size: 1.4rem;
    line-height: lineHeight(26, 14);
    color: $color1;
    background-color: $attention;
    @include media(breakS) {
      top: -25%;
      font-size: 1.2rem;
    }
  }
  &__check {
    margin-bottom: 30px;
    line-height: lineHeight(28);
  }
}
input[type=text] {
  width: 60%;
  height: 43px;
  border: none;
  border-radius: 3px;
  margin-top: 40px;
  margin-bottom: 25px;
  background-color: $bg-section;
  outline: none;
  @include media(breakS) {
    width: 100%; 
  }
}
textarea {
  width: 80%;
  height: 180px;
  border: none;
  border-radius: 3px;
  margin-top: 40px;
  margin-bottom: 25px;
  background-color: $bg-section;
  outline: none;
  resize: none;
  @include media(breakS) {
    width: 100%; 
  }
}
input[type=checkbox] {
  width: 20px;
  height: 20px;
  border: 1px solid $shadow;
  margin: 0 10px 0 0;
  outline: none;
}
input[type=submit] {
  display: block;
  padding: 0;
  margin: 0 auto;
  border: none;
  font-weight: bold;
  outline: none;
}
.end-message {
  display: block;
  margin-top: 50px;
  text-align: center;
  font-size: 1.8rem;
}
a.end-message {
  color: darkcyan;
  text-decoration: underline darkcyan;
}
.false-message {
  margin-top: 50px;
  text-align: center;
  display: none;
}