html {
  font-size: 62.5%;
}
body {
  color: $main_color;
  font-family: "Nato Sans JP", "Hiragino Kaku Gothic ProN", sans-serif;
  font-size: 1.6rem;
  background-color: #fff;
  @include media(breakM) {
    font-size: 1.4rem;
  }
}
img {
  display: block;
}
.btn {
  width: 155px;
  height: 37px;
  padding: 8px 0;
  background: $btn_linearGradient;
  border-radius: 10px;
  box-shadow: 0 3px 6px $box_shadow;
  text-align: center;
  font-size: 1.4rem;
  line-height: lineHeight(20, 14);
  color: $color1;
  @include media(breakM) {
   font-size: 1.2rem;
   line-height: 1.8;   
  }
  @include media(breakS) {
    margin: 0 auto;
  }
}
.section__title {
  margin-bottom: 10px;
  text-align: center;
  font-size: 3.2rem;
  line-height: lineHeight(28, 32);
  @include media(breakM) {
    font-size: 2.8rem;   
  }
  @include media(breakS) {
    font-size: 2.6rem;
  }
}
.section__title-eg {
  text-align: center;
  color: $sub-title;
  line-height: lineHeight(24);
}
@keyframes header-height {
  0% {
    height: 0;
  }
  100%{
    height: 94px;
  }
}