.blog {
  padding: 55px 0 90px;
  background-color: $bg-section;
  font-weight: bold;
  @include media(breakS) {
    padding: 55px 0 70px; 
  }
  &-list {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;
    max-width: 1366px;
    padding: 0 70px;
    margin: 50px auto 0;
    @include media(breakM) {
      padding: 0 10px; 
    }
    @include media(breakS) {
      display: block;
    }
    &__item {
      position: relative;
      max-width: 32%;
      height: 387px;
      background-color: $color1;
      box-shadow: 0 3px 6px $box_shadow;
      @include media(breakS) {
        max-width: 70%;
        margin: 0 auto 30px;
      }
      @include media(breakXS) {
        max-width: 100%;
      }
    }
  }
  &__image {
    max-width: 100%;
    height: auto;
    @include media(breakS) {
      height: 70%;
      width: 100%;
    }
  }
  &__heading {
    padding-top: 15px;
    padding-left: 20px;
    font-size: 1.8rem;
    line-height: lineHeight(26, 18);
    @include media(breakM) {
      font-size: 1.6rem;
    }
    @include media(breakS) {
      font-size: 1.4rem;
    }  
  }
  &__date {
    position: absolute;
    bottom: 10px;
    right: 8%;
    display: block;
    text-align: right;
    line-height: lineHeight(26);
  }
}